window.addEventListener('DOMContentLoaded', () => {

    'use strict';

    let showUp = require("./parts/scroll"),
        images = require("./parts/images");



    // showUp();
    images();
    
    new WOW().init();


    

});

$(document).ready(function(){

    $('.slider').slick({
        infinite: true,
        slidesToShow: 3,
        slidesToScroll: 1,
        centerMode: true,
        centerPadding: 0,
        speed: 1000,
        dots: true,
        prevArrow: '<button type = "button" class = "arrows slick-prev"><img src="icons/arrow-prev.png"></img></button>',
        nextArrow: '<button type = "button" class = "arrows slick-next"><img src="icons/arrow-next.png"></img></button>'
    });


    $(".menu, button, .footer__nav").on("click","a", function (event) {
        event.preventDefault();
        var id  = $(this).attr('href'),
            top = $(id).offset().top;
        $('body,html').animate({scrollTop: top}, 1500);
    });

    $('form').submit(function(e) {
        e.preventDefault();
        $.ajax({
            type: "POST",
            url: "mailer/smart.php",
            data: $(this).serialize()
        }).done(function() {
            $(this).find("input").val("");
            $('form, .appointment .title, .appointment__info, .appointment__require').fadeOut();
            $('body').css({'overflow' : 'hidden'});
            $(".overlay").fadeIn('slow');

            $('form').trigger('reset');
        });
        return false;
    });

    $('.close').on('click', function() {
        $(".overlay").fadeOut();
        $('body').css({'overflow' : ''});
        $('form, appointment title, appointment__info, .appointment__require').fadeIn();
    });


    $.fn.setCursorPosition = function(pos) {
        if ($(this).get(0).setSelectionRange) {
          $(this).get(0).setSelectionRange(pos, pos);
        } else if ($(this).get(0).createTextRange) {
          var range = $(this).get(0).createTextRange();
          range.collapse(true);
          range.moveEnd('character', pos);
          range.moveStart('character', pos);
          range.select();
        }
      };
      $('input[name="phone"]').click(function(){
        $(this).setCursorPosition(3);  // set position number
      });

});
